import { LOAD_PROJECTS } from '../actions/types';

const initialState = {
  projectsArr: [],
};

export default function (state = initialState, action) {
  const { type, payload } = action;
  switch (type) {
    case LOAD_PROJECTS:
      return {
        ...state,
        projectsArr: payload,
      };
    default:
      return state;
  }
}
